@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: "Open Sans", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 24px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  max-width: 100%;
  height: auto;
}

h1 {
  font-size: 60px;
  font-weight: 300;
  line-height: 1;
}

h2 {
  font-size: 48px;
  font-weight: 300;
  line-height: 1;
}

h3 {
  font-size: 30px;
  font-weight: 600;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

a:hover {
  color: #C00000;
  text-decoration: none;
  cursor: pointer;
  transition: 0.3s;
}

ul {
    list-style-type: none;
}

.content-wrapper {
  padding: 64px 20%;
}

.primary-image {
  border-bottom: 1px solid #cccccc;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

@media only screen and (max-width: 768px) {
  .content-wrapper {
    padding: 64px 10%;
  }
}
.ngc-button {
  border: 0;
  cursor: pointer;
  display: inline-block;
  line-height: 1;
}

.ngc-button--primary {
  color: white;
  width: auto;
  height: 220px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  padding-right: 24px;
}

.ngc-button--secondary {
  color: black;
  font-size: 20px;
  background-color: transparent;
}

.ngc-button--primary svg {
  margin-top: 47px;
}

.ngc-button--primary p {
  line-height: 1.4;
  margin-top: 41px;
  margin-left: 32px;
  font-size: 30px;
}

.ngc-button--secondary-dark {
  color: white;
}

.ngc-button--secondary.selected {
  color: #C00000;
}

.ngc-button--secondary-dark.selected {
  color: #ff2929;
}
.ngc-logo-bordered {
    border: 1px solid white;
    padding: 1.4em;
}
.wrapper{border-bottom:1px solid #cccccc;padding:36px 140px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.wrapper img{display:inline-block}.nav-items{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.nav-items *{font-size:20px}.nav-items li:hover .overflow{display:-webkit-flex;display:flex}.nav-items li+li{margin-left:20px;cursor:pointer}.overflow{display:none;position:absolute;background:#c00000;-webkit-justify-content:space-between;justify-content:space-between;-webkit-flex-direction:column;flex-direction:column;border-radius:8px}.overflow a{padding:20px 30px;color:white !important}.overflow a:hover{color:black !important}.overflow a+a{border-top:1px solid white}

footer {
  font-size: 20px;
  color: white;
  background-color: black;
  padding: 80px 140px;
}

.ngc-footer-top {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-align-items: center;
            align-items: center;
}

.ngc-footer-bottom {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 48px;
    font-size: 16px;
}

.ngc-footer-bottom .locations {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    line-height: 1.6;
}

.ngc-footer-bottom .locations div, .ngc-footer-bottom div p {
    margin: 10px 80px 10px 0;
}

@media only screen and (max-width: 1024px) {
    footer {
        padding: 48px 48px;
    }
}

@media only screen and (max-width: 768px) {
    footer {
        padding: 26px 48px;
    }
}
.cta-wrapper {
  border-bottom: 1px solid #cccccc;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.cta-wrapper h1 {
  margin-left: 140px;
  -webkit-flex: 2 1;
          flex: 2 1;
}

@media only screen and (max-width: 1080px) {
  .cta-wrapper h1 {
    margin-left: 50px;
  }
}

@media only screen and (max-width: 800px) {
  .cta-wrapper h1 {
    margin-left: 50px;
  }
}

.primary-image > .right {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.primary-image > img {
  margin: 0 62px;
}

.primary-image > .right > img {
  margin: 0 72px;
}

.primary-image > .right > div {
  padding: 106px 72px;
  font-size: 30px;
  text-align: center;
  background-color: #c00000;
  color: white;
}

.wrapper-values {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  grid-template-columns: repeat(3, 1fr);
}

.wrapper-values > div:first-child {
  background-color: #c00000;
  color: white;
}

@media only screen and (max-width: 800px) {
  .primary-image > .right > img {
    margin: 0 16px;
  }
  .primary-image > .right > div {
    padding: 42px 42px;
    font-size: 20px;
  }
}

.value-item{border-top:1px solid #cccccc;padding:80px 85px;transition:0.3s;-webkit-flex:1 1 20%;flex:1 1 20%}.value-item .value-icon{position:relative;width:150px;height:150px;border:1px solid #b3aab0;background-color:white;border-radius:100%;float:right;top:-150px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.value-item .value-icon img{width:75%;margin:auto;height:auto}.value-item+.value-item{border-left:1px solid #cccccc}.value-item.first{background-color:transparent !important;color:black !important}.value-item:hover{background-color:#c00000;color:white}@media only screen and (max-width: 1080px){.value-item{padding:12px 48px;-webkit-flex:1 1 30%;flex:1 1 30%}.value-icon{top:20px !important;width:80px !important;height:80px !important}}@media only screen and (max-width: 800px){.value-item{padding:12px 48px;-webkit-flex:1 1 50%;flex:1 1 50%}}

.network-item{display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;padding:32px 100px 100px 100px;background-color:white;-webkit-filter:drop-shadow(0 1px 10px rgba(0,0,0,0.156863));filter:drop-shadow(0 1px 10px rgba(0,0,0,0.156863));transition:0.3s}.network-item img{padding:0 92px;width:70%;height:auto}.network-item .img-c{-webkit-flex:1 1;flex:1 1;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.network-item .content{-webkit-flex:2 1;flex:2 1}.network-item p{font-size:20px}.network-item--small{display:block;padding:38px}.network-item--small .img-c{width:100px;height:100px;border-radius:100%;border:1px solid black;margin:0 auto}.network-item--small .img-c img{width:60%;height:60%;padding:20%}.network-item--small .content h3{font-size:20px;text-align:center}.network-item--small p{text-align:center;font-size:16px}.network-item:hover{-webkit-filter:drop-shadow(0 1px 20px rgba(0,0,0,0.156863));filter:drop-shadow(0 1px 20px rgba(0,0,0,0.156863));z-index:99}@media only screen and (max-width: 800px){.network-item{padding:38px !important;-webkit-flex:1 1 50%  !important;flex:1 1 50%  !important}.network-items{padding:0 !important}}

.network-items{display:-webkit-flex;display:flex;-webkit-flex-flow:row wrap;flex-flow:row wrap;grid-template-columns:repeat(2, 1fr);padding:80px 100px}.network-items .network-item{margin:0 20px;margin-bottom:40px;-webkit-flex:0 0 35%;flex:0 0 35%}

.process-items{display:-webkit-flex;display:flex;-webkit-flex-flow:row wrap;flex-flow:row wrap;grid-template-columns:repeat(3, 1fr)}.process-items .network-item--small{margin-bottom:40px;-webkit-flex:1 1 25% !important;flex:1 1 25% !important}.process-items .network-item--small:nth-child(even){margin-left:20px;margin-right:20px}

